import React, { useContext } from "react";
import { MatchContext } from "../Match";

function Spell() {
  const matchContext = useContext(MatchContext);

  return (
    <div className="flex flex-col gap-1">
      <SpellItem spellName={matchContext.participant?.summoner1Id} />
      <SpellItem spellName={matchContext.participant?.summoner2Id} />
    </div>
  );
}

function SpellItem(props: { spellName: string | null | undefined }) {
  return (
    <img
      className="rounded-lg w-8"
      src={`http://ddragon.leagueoflegends.com/cdn/13.17.1/img/spell/${props.spellName}.png`}
      alt="spell"
      loading="lazy"
    />
  );
}

export default Spell;
