import React, { createContext } from "react";
import GameInfo from "./MatchItems/GameInfo";
import ScoreBoard from "./MatchItems/ScoreBoard";
import Spell from "./MatchItems/Spell";
import ItemList from "./MatchItems/ItemList";
import SummonerList from "./MatchItems/SummonerList";
import Detail from "./MatchItems/Detail";
import ChampionIcon from "./MatchItems/ChampionIcon";
import { MatchEntity } from "../../../models";
import { NavigatedSummoner } from "../../../types/context/navigatedSummoner";

export const MatchContext = createContext<NavigatedSummoner>({
  index: undefined,
  match: undefined,
  participant: undefined,
});

function Match(props: {
  match: MatchEntity;
  puuid: string | undefined;
  index: number;
}) {
  const navigatedSummonerMatchData = props.match.teams
    ?.flatMap((matchData) =>
      matchData.participants?.find((summoner) => summoner.puuid === props.puuid)
    )
    .filter((navigatedSummoner) => navigatedSummoner != null)[0];

  const navigatedSummonerIsWin = props.match.teams?.find(
    (teamData) => teamData.teamId === navigatedSummonerMatchData?.teamId
  )?.win;

  // TODO: 화면이 새로 고쳐지면(다른 사람의 전적을 확인했을 때) 모든 컴포넌트가 초기화되도록 변경
  return (
    <MatchContext.Provider
      value={{
        index: props.index,
        participant: navigatedSummonerMatchData,
        match: props.match,
      }}
    >
      <div
        className={`flex flex-col border-2 ${
          navigatedSummonerIsWin
            ? "border-blue-400 bg-blue-200"
            : "border-rose-400 bg-rose-200"
        }  gap-1 min-h-[120px] text-center rounded-md py-1 justify-center`}
      >
        <div className="flex flex-grow gap-10 items-center place-self-center">
          <GameInfo mode="normal" />
          <div className="flex gap-2">
            <ChampionIcon />
            <Spell />
            <ItemList />
          </div>
          <ScoreBoard />
          <SummonerList participants={props.match.teams} />
        </div>

        <Detail isWin={navigatedSummonerIsWin} />
      </div>
    </MatchContext.Provider>
  );
}

export default Match;
