import React, { useContext } from "react";
import { ParticipantEntity } from "../../../../models";
import { ITEM_IMAGE_CDN } from "../../../../config/host";
import { MatchContext } from "../Match";

function ItemList() {
  const matchContext = useContext(MatchContext);

  const sortedInventory = [
    matchContext.participant?.item0,
    matchContext.participant?.item1,
    matchContext.participant?.item2,
    matchContext.participant?.item6,
    matchContext.participant?.item3,
    matchContext.participant?.item4,
    matchContext.participant?.item5,
  ];

  return (
    <div className="grid grid-cols-4 gap-1">
      {sortedInventory.map((itemId) => {
        return <Item itemId={itemId} />;
      })}
    </div>
  );
}

function Item(props: { itemId?: number }) {
  return (
    <div className="w-8 h-8 bg-gray-200 rounded-md overflow-hidden">
      {props.itemId !== 0 ? (
        <img
          src={`${ITEM_IMAGE_CDN}${props.itemId}.png`}
          alt="item"
          loading="lazy"
        />
      ) : (
        <></>
      )}
    </div>
  );
}

export default ItemList;
