import React, { useContext, useState } from "react";
import { useRecoilValue } from "recoil";
import { matchesDataState } from "../../../../recoil/matchesData";
import { MatchContext } from "../Match";

// interface Props {
//   user: ParticipantEntity | undefined;
//   match: MatchEntity | undefined;
// }
type Score = {
  kills: number;
  deaths: number;
  assists: number;
  cs: number;
  ward: number;
};

function ScoreBoard() {
  const matchContext = useContext(MatchContext);
  const [rankedScoreVisible, setRankedScoreVisible] = useState(false);
  const matchData = useRecoilValue(matchesDataState).at(
    matchContext.index ?? 0
  );
  const scoreData: {
    score: Score;
    rank: Score;
    average: string;
    engage: number;
  } = {
    score: {
      kills: matchContext.participant?.kills!,
      deaths: matchContext.participant?.deaths!,
      assists: matchContext.participant?.assists!,
      cs:
        (matchContext.participant?.totalMinionsKilled ?? 0) +
        (matchContext.participant?.neutralMinionsKilled ?? 0)!,
      ward: matchContext.participant?.wardsPlaced!,
    },
    rank: {
      kills:
        matchContext.match?.teams
          ?.flatMap((team) =>
            team.participants?.flatMap((summoner) => summoner.kills)
          )
          .sort((a, b) => b! - a!)
          .findIndex((kills) => kills === matchContext.participant?.kills)! + 1,
      deaths:
        matchContext.match?.teams
          ?.flatMap((team) =>
            team.participants?.flatMap((summoner) => summoner.deaths)
          )
          .sort((a, b) => b! - a!)
          .findIndex((deaths) => deaths === matchContext.participant?.deaths)! +
        1,
      assists:
        matchContext.match?.teams
          ?.flatMap((team) =>
            team.participants?.flatMap((summoner) => summoner.assists)
          )
          .sort((a, b) => b! - a!)
          .findIndex(
            (assists) => assists === matchContext.participant?.assists
          )! + 1,
      cs:
        matchContext.match?.teams
          ?.flatMap((team) =>
            team.participants?.flatMap(
              (summoner) =>
                summoner.totalMinionsKilled! + summoner.neutralMinionsKilled!
            )
          )
          .sort((a, b) => b! - a!)
          .findIndex(
            (cs) =>
              cs ===
              matchContext.participant?.totalMinionsKilled! +
                matchContext.participant?.neutralMinionsKilled!
          )! + 1,

      ward:
        matchContext.match?.teams
          ?.flatMap((team) =>
            team.participants?.flatMap((summoner) => summoner.wardsPlaced)
          )
          .sort((a, b) => b! - a!)
          .findIndex(
            (ward) => ward === matchContext.participant?.wardsPlaced
          )! + 1,
    },
    average: (
      ((matchContext.participant?.kills ?? 0) +
        (matchContext.participant?.assists ?? 0)) /
      (matchContext.participant?.deaths ?? 0)
    ).toFixed(2),
    engage: Math.round(
      (((matchContext.participant?.kills ?? 0) +
        (matchContext.participant?.assists ?? 0)) /
        (matchData?.teams?.at(1)?.objectives?.champion?.kills as number)) *
        100
    ),
  };

  console.log(
    matchContext.match?.teams
      ?.flatMap((team) =>
        team.participants?.flatMap((summoner) => summoner.kills)
      )
      .sort((a, b) => b! - a!)
  );

  return (
    <div
      className="flex justify-center cursor-pointer select-none w-72"
      onClick={() => {
        setRankedScoreVisible(!rankedScoreVisible);
      }}
    >
      <table className="text-gray-600">
        <thead>
          <tr className="text-xs">
            <th className="w-16">킬</th>
            <th className="w-16 text-red-600">데스</th>
            <th className="w-16">어시스트</th>
            <th className="w-16">
              {matchContext.participant?.individualPosition === "UTILITY"
                ? "와드"
                : "CS"}
            </th>
          </tr>
        </thead>
        <tbody>
          <tr className="text-xl font-extrabold">
            <td>
              <div className="flex gap-1 justify-center">
                {(rankedScoreVisible
                  ? scoreData.rank.kills
                  : scoreData.score.kills) ?? 0}
                {rankedScoreVisible && Rank()}
              </div>
            </td>
            <td className="text-red-600">
              <div className="flex gap-1 justify-center">
                {(rankedScoreVisible
                  ? scoreData.rank.deaths
                  : scoreData.score.deaths) ?? 0}
                {rankedScoreVisible && Rank()}
              </div>
            </td>
            <td>
              <div className="flex gap-1 justify-center">
                {(rankedScoreVisible
                  ? scoreData.rank.assists
                  : scoreData.score.assists) ?? 0}
                {rankedScoreVisible && Rank()}
              </div>
            </td>
            <td>
              <div className="flex gap-1 justify-center">
                {matchContext.participant?.individualPosition === "UTILITY"
                  ? rankedScoreVisible
                    ? scoreData.rank.ward
                    : scoreData.score.ward
                  : rankedScoreVisible
                  ? scoreData.rank.cs
                  : scoreData.score.cs}
                {rankedScoreVisible && Rank()}
              </div>
            </td>
          </tr>
          <tr className="text-xs">
            <td colSpan={3}>
              <div className="flex gap-3 justify-center">
                <div>{`평점 ${scoreData.average}`}</div>
                <div>
                  {`킬관여
                  ${scoreData.engage}%`}
                </div>
              </div>
            </td>
            <td>
              {(matchContext.participant?.individualPosition === "UTILITY"
                ? matchContext.participant.wardsPlaced! /
                  new Date(
                    (matchContext.match?.gameDuration as number) * 1000
                  ).getMinutes()!
                : ((matchContext.participant?.totalMinionsKilled as number) +
                    (matchContext.participant
                      ?.neutralMinionsKilled as number)) /
                  new Date(
                    (matchContext.match?.gameDuration as number) * 1000
                  ).getMinutes()
              ).toFixed(1)}
              /분
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}

function Rank() {
  return <div className="text-xs font-semibold self-end mb-[4px]">등</div>;
}

export default ScoreBoard;
